.menu-toggle {
  position: relative;
  width: 66px;
  height: 66px;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.menu-toggle::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 39px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: #ffffff 0 -11px 0 0, #ffffff 0 11px 0 0;
}
.menu-toggle--cross::before {
  top: 47%;
  left: 20%;
  transform: rotate(45deg);
  width: 38px;
  box-shadow: none;
}
.menu-toggle--cross::after {
  position: absolute;
  content: "";
  top: 47%;
  left: 20%;
  transform: rotate(135deg);
  width: 38px;
  height: 4px;
  background-color: #ffffff;
}
.menu-toggle:hover::before {
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: rgba(255, 255, 255, 0.8) 0 -11px 0 0, rgba(255, 255, 255, 0.8) 0 11px 0 0;
}
.menu-toggle--cross:hover,
.menu-toggle--cross:focus {
  opacity: 0.8;
}
.menu-toggle--cross:hover::before,
.menu-toggle--cross:focus::before {
  box-shadow: none;
  background-color: #ffffff;
}
.menu-toggle--cross:hover::after,
.menu-toggle--cross:focus::after {
  background-color: #ffffff;
}
.menu-toggle:active::before {
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: rgba(255, 255, 255, 0.6) 0 -11px 0 0, rgba(255, 255, 255, 0.6) 0 11px 0 0;
}
.menu-toggle--cross:active {
  opacity: 0.6;
}
.menu-toggle--cross:active::before {
  box-shadow: none;
  background-color: #ffffff;
}
.menu-toggle--cross:active::after {
  background-color: #ffffff;
}
.menu-toggle__text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1440px) {
